<template>
  <s-header :name="type === 'blod' ? '血氧饱和度' : '体温'" rightName="历史数据" :rightPress="passHistoryData" />
  <div class="blood-oxygen" >
    <select-date @changeDate="changeDate" :onAdd="onAdd" :onReresh="onReresh">
      <template #cont >
        <div v-if="type === 'blod'" >
          <div ref="chartDom" class="wave-echart" id='waveEchart' ></div>
          <div class="current-status" >
            <div class="text" >当前状态</div>
            <div class="status" >{{ getTitle(booldObj.type)}}</div>
          </div>
          <proportion :status="typeList" :currentStatus="booldObj.type"/>
        </div>
        <div v-else >
          <div ref="chartDom" style="width: 250px; height: 250px" class="wave-echart" id='waveEchart' ></div>
        </div>
      </template>
    </select-date>
    <div class="tips-content-box" >
      <tipsItem  type="hint" :imgUrl="require('@/assets/ic_data@2x.png')" title="数据解读" :desc="type !== 'blod'? tempObj.typeStr : booldObj.typeStr" />

      <tipsItem  type="hint" :style="{ marginTop: '15px' }" :imgUrl="require('@/assets/ic_tishi@2x.png')" title="温馨提示" :desc="type === 'blod'? oxgendesc : tempDesc" />

      <tipsItem type="list" :page = "type" :clickDeleteItem="deleteItem" :recordList = "recordList" :style="{ marginTop: '15px' }" :imgUrl="require('@/assets/ic_jilu@2x.png')" title="今日记录" desc="血氧是指血液中的氧气，人体正常含氧量为90%以上。血液中含氧量越高，人的新陈代谢就越好。日常生活中请注意合理饮食，保证充足、高质量的睡眠，作息规律。" />
    </div>

  </div>
</template>

<script>
import { onMounted, ref,reactive,toRefs } from "vue";
import sHeader from '@/components/SimpleHeader.vue'
import selectDate from "@/components/selectDate.vue"
import 'echarts-liquidfill';
import * as echarts from "echarts";
import proportion from "@/components/proportion2.vue"
import tipsItem from "../component/tipsItem.vue"
import {useRoute, useRouter} from "vue-router";
import { useGetecharts } from "../hooks";
const { getOxygen, getListByTime, deleteOxygen, deleteTemperature, getTemperature, getTempListByTime } = require("../../../../service/blood-oxygen");
import { getUserId } from  '@/utils/util'
import moment from 'moment'
import TrackType from '../../components/TrackType'


export default {
  setup(props){

    const temDes  = ['低', '正常', '低热', '中热', '高热', '超高热']
    let chartDom = ref(null);
    let {params: {type}} = useRoute();
    let {push} = useRouter();
    let {getEchartsOne, getEchartsTwo} = useGetecharts()
    const oxgendesc = "血氧是指血液中的氧气，人体正常含氧量为90%以上。血液中含氧量越高，人的新陈代谢就越好。日常生活中请注意合理饮食，保证充足、高质量的睡眠，作息规律。"
    const tempDesc = ['1.体温是会随着环境温度而有所改变的，正常波动24h内不会超过1℃。',
      '2.清晨体温略低，下午略高；运动或进食后体温略高；',
      '3.老年人体温略低；','4.月经期前或妊娠期女性体温略高。']

    const state = reactive({
      recordList:[], // 记录
      typeList: [{title: '过低', id: 1}, {title: '偏低', id: 2}, {title: '正常', id: 3}],
      booldObj: {
        bloodOxygen: 0,// 血氧饱和度
        type:0,
        typeStr: "暂无数据",
      } , // 血氧
      tempObj: {
        type:0,
        typeStr: "暂无数据",
        inputTime: '',
        temperature: 0,
      }, // 体温
      data: {},
    })

    const deleteItem =  async (id) => {
     const res = type === "blod" ? await deleteOxygen(id)  : await deleteTemperature(id)
      const { code, data } = res
      if(code==200){
        if( type === "blod"){
          _getOxygen(moment().format('YYYY-MM-DD'))
          _getListByTime(moment().format('YYYY-MM-DD'),moment().add(1).format('YYYY-MM-DD'))
        }else{
          _getTemperature(moment().format('YYYY-MM-DD'))
          _getTempListByTime(moment().format('YYYY-MM-DD'),moment().add(1).format('YYYY-MM-DD'))
        }
      }
    }

    const getTitle = () => {
      let str = ""
      state.typeList.map((item) => {
        if(item.id === state.booldObj.type){
          str = item.title
        }
      })
      return str
    }

    const _getListByTime =  async (starttime,endtime) => {
      const res = await getListByTime(getUserId(),starttime,endtime)
      const { code, data =[] } = res
      if(code === 200){
        state.recordList = data
      }
    }

    const _getTempListByTime =  async (starttime,endtime) => {
      const res = await getTempListByTime(getUserId(),starttime,endtime)
      const { code, data =[] } = res
      if(code === 200){
        state.recordList = data
      }
    }



    const _getOxygen = async (time) => {
      const res1 = await getOxygen(getUserId(),time)
      const { code, data } = res1
      let myEcharts = echarts.init(chartDom.value);
      if(code === 200 && data){
        state.booldObj = data
        console.log("myEcharts", echarts)
      }else{
        state.booldObj = {
          bloodOxygen: 0,// 血氧饱和度
          type:0,
          typeStr: "暂无数据",
        }
      }
      const value = state.booldObj.bloodOxygen / 100
      myEcharts.setOption(type === "blod" ? getEchartsOne(value) : getEchartsTwo())
    }

    const _getTemperature = async (time) => {
      const res1 = await getTemperature(getUserId(),time)
      const { code, data } = res1
      let myEcharts = echarts.init(chartDom.value);
      if(code === 200 && data){
        state.tempObj = data
        console.log("myEcharts", echarts)
      }else{
       state.tempObj = {
          type:0,
              typeStr: "暂无数据",
              inputTime: '',
              temperature: 0,
        }
      }
      const value = state.tempObj.temperature/100
      myEcharts.setOption( getEchartsTwo(value))
    }

    const initData = () => {
      type === "blod" ? _getListByTime(moment().format('YYYY-MM-DD'),moment().add(1).format('YYYY-MM-DD'))
      : _getTempListByTime(moment().format('YYYY-MM-DD'),moment().add(1).format('YYYY-MM-DD'))
     type === "blod" ? _getOxygen(moment().format('YYYY-MM-DD')) : _getTemperature(moment().format('YYYY-MM-DD'))
    }

    onMounted(() => {
      if(!chartDom.value) return;
      initData()
    })


    function changeDate(date){
      console.log("data", date)
    }
    function passHistoryData(){
      push({ path:"/historyOxygenTempRecord", query: { type: type}})
    }
    const onAdd = () =>{
      console.log("type",type)
      push({ path:"/bloodOxygen-manualRecord", query: { type: type}})
    }
    const onReresh = () =>{

    }
    return {
      changeDate,
      chartDom,
      passHistoryData,
      type,
      onReresh,
      onAdd,
      getTitle,
      ...toRefs(state),
      deleteItem,
      tempDesc,
      oxgendesc,
      temDes
    }
  },
  components: {
    sHeader,
    selectDate,
    proportion,
    tipsItem,
    TrackType,
  }
}
</script>

<style lang="less" scoped>
  .blood-oxygen{
    padding: 10px;
    background: #eee;
    height: calc(100vh - 64px);
    overflow: scroll;
  }
  .wave-echart{
    width: 200px;
    height: 200px;
    margin: auto;
    margin-top: 20px;
  }

  .current-status{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 6px 0;
    .text{
      font-size: 13px;
      color: #bbb;
    }
    .status{
      font-size: 23px;
      color: #06B48D;
    }
  }
  .tips-content-box{
    margin-top: 20px;
  }
</style>
