<template>
  <div class="tips-item" >
      <div class="tips-item-title" >
        <img :src="imgUrl"/>
        <div>{{ title }}</div>
        </div>
      <div v-if="type==='list'" class="tips-item-cont" >
        <div class="empty_tip" v-show="recordList.length === 0">
          暂无数据
        </div>
        <div  v-show="recordList.length !== 0" v-for="item in recordList" :key="item.id" class="tips-item-list-item">

          <div class="data-tip-item">
            <div>
              {{ formatHHSS( item.inputTime) }}
            </div>
            <div> {{ item.datasouce === 0 ? '手动录入': '设备获取' }}</div>
            <div><span :style="{ color:'#00bc88',marginRight: '10px' }">{{ page === 'blod' ? item.oxygen +'%': item.temperature+'℃' }}</span> {{ page !== 'blod' ?  temDes[item.type - 1] :des[item.type - 1]}}</div>
            <div>
              <img @click="clickDeleteItem(item.id)" :style="{ marginLeft:'18px', height: '15px'}" :src="require('@/assets/ic_delete@2x.png')">
            </div>
          </div>
        </div>
      </div>
      <div v-else-if="type==='hint'" class="tips-item-cont" >
        <div v-if="isArray(desc)">
          <p v-for="(item,index) in desc" :key="index">
            {{ item }}
          </p>
        </div>
        <div v-else-if="!isArray(desc)">
          {{ desc || "暂无数据" }}
        </div>
      </div>
  </div>
</template>

<script>
import moment from 'moment'
export default {
  props:{
    clickDeleteItem: Function,
    recordList: Array,
    imgUrl: String,
    title: String,
    desc: String,
    type: String,
    page: String
  },
  setup(props){
    const des = ['正常', '偏低', '过低']
    const temDes  = ['低', '正常', '低热', '中热', '高热', '超高热']

    function isArray(o){
      return Object.prototype.toString.call(o)=='[object Array]';
    }

    const formatHHSS =  (time) => {
     return moment(time).format('HH:mm')
    }
    return {
      formatHHSS,
      des,
      temDes,
      isArray
    }
  }
}
</script>

