export const useGetecharts = () => {

    function getEchartsOne (value) {
        return {
            title: {// 标题
              text: '血氧饱和度',
              textStyle: {// 标题的样式
                color: '#fff', // 字体颜色
                fontFamily: 'Microsoft YaHei', // 字体
                fontSize: 12,
                fontWeight: '400' // 文字的垂直方式
              },
              left: 'center', // 定位
              bottom: '35%'
            },
            series: [{
              type: 'liquidFill',
              radius: '95%',
              amplitude: 6,
              waveAnimation: true,
              data: [{
                value: value,
                itemStyle: {
                  normal: {
                    opacity: 0.5, // 波浪的透明度
                    shadowBlur: 0,
                    color: 'rgb(237, 110, 115)'
                  }
                }
              },
              {
                value: value,
                itemStyle: {
                  normal: {
                    color: 'rgb(237, 110, 115)',
                    opacity: 0.9, // 波浪的透明度
                    shadowBlur: 0,
                  }
                }
              }],
              outline: {
                // show: true , //是否显示轮廓 布尔值
                borderDistance: 2 , // 外部轮廓与图表的距离 数字
                itemStyle: {
                  borderColor: 'rgb(237, 110, 115)', // 边框的颜色
                  borderWidth: 1 // 边框的宽度
                  // shadowBlur: 5 , //外部轮廓的阴影范围 一旦设置了内外都有阴影
                  // shadowColor: '#000' //外部轮廓的阴影颜色
                }
              },
              backgroundStyle: {
                color: 'rgb(255, 197, 197)' // 图表的背景颜色
              },
              label: { // 数据展示样式
                show: true,
                color: '#fff',
                insideColor: '#fff',
                fontSize: 30,
                fontWeight: 400,
                position: ["50%", "45%"]
              }
            }]
          }
    }

    function getEchartsTwo(value){
        return {
            series: [{
                type: 'gauge',
                startAngle: 220,
                endAngle: -40,
                min: 0.3499,
                max: 0.41111,
                splitNumber: 6,
                axisLine: {
                    lineStyle: {
                        width: 18,
                        color: [
                            [0.17, '#FF6E76'],
                            [0.34, '#FDDD60'],
                            [0.51, '#58D9F9'],
                            [0.68, '#7CFFB2'],
                            [0.85, '#58D9F9'],
                            [1, '#7CFFB2'],
                        ]
                    }
                },
                pointer: {
                    icon: 'path://M12.8,0.7l12,40.1H0.7L12.8,0.7z',
                    length: '12%',
                    width: 20,
                    offsetCenter: [0, '-60%'],
                    itemStyle: {
                        color: 'auto'
                    }
                },
                axisTick: {
                    length: 0,
                    lineStyle: {
                        color: 'auto',
                        width: 6
                    }
                },
                splitLine: {
                    length: 20,
                    lineStyle: {
                        color: 'auto',
                        width: 0
                    }
                },
                axisLabel: {
                    color: '#464646',
                    fontSize: 12,
                    distance: -52,
                    formatter: function (value) {
                        let arr = [
                            {title: "低", isTrue: value < 0.36},
                            {title: "正常", isTrue: value > 0.36 && value <= 0.37},
                            {title: "低热", isTrue: value > 0.37 && value <= 0.38},
                            {title: "中热", isTrue: value > 0.38 && value <= 0.39},
                            {title: "高热", isTrue: value > 0.39 && value <= 0.41},
                            {title: "超高热", isTrue: value > 0.41}
                        ]
                        return arr.find(({isTrue}) => isTrue).title
                    }
                },
                title: {
                    offsetCenter: [0, 13],
                    fontSize: 14
                },
                detail: {
                    fontSize: 30,
                    offsetCenter: [0, "-13"],
                    valueAnimation: true,
                    formatter: function (value) {
                        return parseFloat(value * 100).toFixed(1) + '℃';
                    },
                    color: 'auto'
                },
                data: [{
                    value: value,
                    name: "当前体温"
                }]
            }]
        }
    }

    return {
        getEchartsOne,
        getEchartsTwo
    }

}
