<template>
    <div style="background: #fff; padding: 10px; border-radius: 5px;" >
        <div class="select-date" >
            <div class="change-date" >
                <div @click="changeCurrentIndex('prev')" class="change-time" >
                    <svg class="svg" aria-hidden="true" style="height: 44px;width: 9px;justify-content: center;align-items: center">
                    <use xlink:href="#icon-goback"></use>
                    </svg>
                </div>
                <div class="time-cont" >
                    <div v-show="currentIndex === 0" style="font-size: 15px;" >今日</div>
                    <span>{{timeFormat}}</span>
                </div>
                <div @click="changeCurrentIndex('next')" style="transform: rotate(180deg); position: relative; top: -2px" class="change-time" >
                    <svg class="svg" aria-hidden="true" style="height: 44px;width: 9px;justify-content: center;align-items: center">
                    <use xlink:href="#icon-goback"></use>
                    </svg>
                </div>
            </div>
            <div @click="visible = true" class="calendar" >
                <img :src="require('@/assets/calendar.png')" />
            </div>
        </div>
        <div class="cont" >
            <slot name="cont" ></slot>
        </div>
        <div class="operation" >
            <div @click="isAddData = true" class="operation-item">
                <img :src="require('@/assets/add@2x.png')" />
                <span>添加数据</span>
            </div>
            <div class="operation-item">
                <img :src="require('@/assets/reloadBAK.png')" />
                <span>获取数据</span>
            </div>
        </div>
        <v-popup
            v-model:show="visible"
            position="bottom"
        >
            <v-datetime-picker
                :formatter="formatter"
                type="date"
                @confirm="confimTime"
            />
        </v-popup>
        <v-popup
            v-model:show="isAddData"
            style="height: 200px;"
            position="bottom"
            closeable
        >
            <div>
                <span class="add-data-title" >添加数据</span>
                <div class="add-data" >
                    <div class="add-data-item" @click="onAdd">
                        <div style="background: rgb(41, 111, 197)" >
                            <img :src="require('@/assets/Input.png')" />
                        </div>
                        <span>手动录入</span>
                    </div>
                    <div class="add-data-item" @click="onReresh">
                        <div style="background: rgb(255, 189, 99)" >
                            <img :src="require('@/assets/binding.png')" />
                        </div>
                        <span>绑定设备</span>
                    </div>
                    <div class="add-data-item" >
                        <div style="background: rgb(255, 189, 99)" >
                            <img :src="require('@/assets/binding.png')" />
                        </div>
                        <span>刷新设备数据</span>
                    </div>
                </div>
            </div>
        </v-popup>
    </div>
</template>

<script>
import { ref, computed, watchEffect, watch } from "vue";
import { useDate } from "../utils/hooks";
import moment from "moment";
import { DatetimePicker, Popup } from "vant";
export default {
    emits:[
        "changeDate"
    ],
  props:{
    onAdd:{
      type:Function
    },
    onReresh:{
      type:Function
    },
  },
    setup(props, {emit}){
        let time = ref(+new Date());
        let currentIndex = ref(0);
        let { getDayDate } = useDate();
        let timeFormat = computed(() => moment(time.value).format("YYYY年MM月DD日"));
        let visible = ref(false);
        let isAddData = ref(false);

        watchEffect(() => { emit("changeDate", time.value) })

        watch(() => currentIndex.value, (newVal) => {
            time.value = getDayDate(newVal);
        })

        function confimTime(val){
            time.value = val;
            visible.value = false
        }

        function changeCurrentIndex(type){
            if(type === "prev"){
                --currentIndex.value
            }else{
                currentIndex.value < 0 && ++currentIndex.value
            }
        }

        function formatter(type, val){
            console.log("type", type)
            let formatJson = {
                "year": `${val}年`,
                "month": `${val}月`,
                "day": `${val}日`,
            }
            return formatJson[type];
        }

        return {
            changeCurrentIndex,
            timeFormat,
            currentIndex,
            visible,
            formatter,
            confimTime,
            isAddData
        }
    },
    components: {
        vDatetimePicker: DatetimePicker,
        vPopup: Popup
    }
}
</script>

